<template>
  <el-container>
    <el-main>
      <el-card shadow="hover">
        <div class="card-title" @click="showprofile = !showprofile">
          <i class="el-icon-user"></i>&nbsp&nbsp
          <span>基本信息</span>
          <i class="el-icon-view" style="float:right;"></i>
        </div>
        <el-collapse-transition>
          <div v-show="showprofile">
            <div class="myprofile">
              <div class="head-portrait">
                <el-avatar :size="80" fit="fit" style="margin-top:8px;margin-bottom:8px;">User</el-avatar>
              </div>
              <div>
                <el-tag>姓名：</el-tag>
                <span style="font-size: 14px;margin-left:10%;font-weight:bold;">高智勇</span>
              </div>
              <div>
                <el-tag>电话：</el-tag>
                <span style="font-size: 14px;margin-left:10%;font-weight:bold;">15295572576</span>
              </div>
              <div>
                <el-tag>邮箱：</el-tag>
                <span style="font-size: 14px;margin-left:10%;font-weight:bold;">1450136519@qq.com</span>
              </div>
              <div>
                <el-tag>博客：</el-tag>
                <span style="font-size: 14px;margin-left:10%;font-weight:bold;">blog.oceaneyeys.cn</span>
              </div>
              <div>
                <el-tag>网站：</el-tag>
                <span style="font-size: 14px;margin-left:10%;font-weight:bold;">oceaneyes.top</span>
              </div>
            </div>
          </div>
        </el-collapse-transition>
      </el-card>

      <el-card shadow="hover">
        <div class="card-title" @click="showskills = !showskills">
          <i class="el-icon-paperclip"></i>&nbsp&nbsp
          <span>技能</span>
          <i class="el-icon-view" style="float:right;"></i>
        </div>

        <el-collapse-transition>
          <div v-show="showskills">
            <div class="skills" v-for="i in skills" >
              <el-tag>{{i.key}}</el-tag>
              <el-progress :percentage="i.value" style="width:68%"></el-progress>
            </div>
          </div>
        </el-collapse-transition>
      </el-card>
    </el-main>
  </el-container>
</template>

<style scoped>
.el-main {
  padding: 10px;
}
.head-portrait {
  align-content: center;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #f1f1f1;
}
.el-card {
  margin-bottom: 6px;
}
.skills {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  margin-top: 16px;
}
.card-title {
  font-weight: bold;
}
.myprofile {
  display: flex;
  flex-direction: column;
}
.myprofile div {
  display: flex;
  flex-direction: row;
  align-items: center;
      margin-top: 14px;
}
</style>

<script>
export default {
  name: "Aside",

  data() {
    return {
      showprofile: true,
      showskills: true,
      skills: [
        { key: "PS", value: 94 },
        { key: "AI", value: 90 },
        { key: "Visio", value: 88 },
        { key: "Xmind", value: 89 },
        { key: "Office", value: 90 },
        { key: "Axure", value: 90 },
        { key: "Sketch", value: 90 },
        { key: "Python", value: 80 },
        { key: "MySQL", value: 70 },
        { key: "H5", value: 78 },
        { key: "VUE", value: 60 }
      ]
    };
  }
};
</script>


